import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
//page
import Article from './page/Article';
import Contacts from './page/Contacts';
import Cooperation from './page/Cooperation';
import DirectForm from './page/DirectForm';
import Faq from './page/faq';
import FaqAnswer from './page/faqAnswer';
import Fees from './page/Fees';
import HomePageDefault from './page/HomePageDefault';
import HowThisWorks from './page/howThisWorks';
import Htmltojsx from './page/htmltojsx';
import News from './page/News';
import Payment from './page/Payment';
import Reviews from './page/Reviews';
import SignIn from './page/SignIn';
import SignUp from './page/SignUp';
// element
import Header from './heder/header';
import Header_reg from './heder/header_reg';
import Footer from './footer/footer';


const App = () => (


    <Router>
        { /*Header /////////////////////////////////////////////// */}
        <div>
            <Switch>
                <Route exact path="/SignUp" component={Header_reg} />
                <Route exact path="/SignIn" component={Header_reg} />
                <Route exact path="/*" component={Header} />
            </Switch>
            { /*Main ///////////////////////////////////////////////// */}
            <Switch>
                <Route exact path="/Article" component={HomePageDefault} />
                <Route exact path="/Contacts" component={Contacts} />
                <Route exact path="/Cooperation" component={Cooperation} />
                <Route exact path="/DirectForm" component={DirectForm} />
                <Route exact path="/Faq" component={Faq} />
                <Route exact path="/FaqAnswer" component={FaqAnswer} />
                <Route exact path="/Fees" component={Fees} />
                <Route exact path="/HowThisWorks" component={HowThisWorks} />
                <Route exact path="/Htmltojsx" component={Htmltojsx} />
                <Route exact path="/News" component={News} />
                <Route exact path="/Payment" component={Payment} />
                <Route exact path="/Reviews" component={Reviews} />
                <Route exact path="/SignIn" component={SignIn} />
                <Route exact path="/SignUp" component={SignUp} />
                <Route exact path="/" component={HomePageDefault} />
            </Switch>
            {/*Footer ///////////////////////////////////////////////// */}
            <Route exact path="/*" component={Footer} />
        </div>
    </Router>

);
export default App;



