import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import ReactDOM from 'react-dom';
class footer extends Component {
  render() {
    return (
      <footer>
        <div className="layout">
          <div className="layout-mid">
            <div className="text">
              © SiteName 2014-2015<br />
              Копирование материалов сайта допускается с&nbsp;указанием ссылки на&nbsp;первоисточник
            </div>
            <div className="links">
              <div className="link">
                <Link to='/DirectForm' >Правила обмена</Link>
              </div>
              <div className="link">
                <Link to='/Cooperation' >Партнерам</Link>
              </div>
              <div className="link">
              <Link to="/News" >Новости</Link>
              </div>
              <div className="link">
              <Link to='/Contacts' >Контакты</Link>
              </div>
              <div className="link">
              <Link to='/faq' >FAQ</Link>
              </div>
            </div>
            <div className="clear" />
          </div>
        </div>
      </footer>
    );
  }
}

export default footer;



